.Playlist {
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow-y: scroll;
    width: 37%;
    max-height: 950px;
    padding: 2.27rem 1.16rem;
    background-color: rgba(1, 12, 63, 0.7);
    box-shadow: 0 4px 2px 2px #000000;
  }
  
  .Playlist input {
    width: 100%;
    border: 0;
    outline: 0;
    background: transparent;
    border-bottom: 1px solid white;
    font-family: sans-serif;
    font-size: 1.3rem;
    font-weight: 500 ;
    color: #fff;
    text-align: center;
    
  }

  /* ::placeholder{
    color:white;
  } */
  
  .Playlist-save {
    cursor: pointer;
    width: 100%;
    padding: 1rem .5rem;
    border-radius: 4px;
    border-width: 0px;
    margin-top: 1.27rem;
    background-color: #6c41ec;
    text-align: center;
    font-size: .83rem;
    transition: background-color .25s;
    color: #fff;
    font-weight: 500;
    text-transform: uppercase;
  }
  
  .Playlist-save:hover {
    background-color: rgba(124, 107, 187, 0.7);
    font-size: 1rem;
    padding: 1.2rem;
  }
  .Playlist-save:active {
    background-color: rgba(124, 107, 187, 0.7);
    font-size: 1.1rem;
    padding: 1.3rem;
  }
  
  ::-webkit-scrollbar {
      width: 0px;
      background: transparent;
  }

  @media only screen and (max-width: 700px) {
    .Playlist {
      width: 100%;
    }
  } 