.Track {
    display: flex;
    align-items: center;
    border-bottom: 1px solid rgba(256, 256, 256, 0.8);
  }
  .Track:hover{
    background-color: rgba(221, 199, 217, 0.2)
  }
  
  .Track-action {
    cursor: pointer;
    margin-left: 0.5rem;
    padding: .5rem;
    font-size: 1.05rem;
    transition: color .25s;
    border: 0px;
    background-color: rgba(0, 0, 0, 0);

    color: #fff;
    background-color: rgba(108, 65, 233, .7);
  }
  
  /* .Track-action:hover {
    color: rgba(265, 265, 265, .5);
  } */
  
  .Track-information {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    /* min-height: 72px; */
    padding-top: .7rem;
    padding-bottom: .9rem;
  }
  
  .Track-information h4 {
    margin-bottom: .42rem;
  }
  
  .Track-information p {
    font-size: .83rem;
    font-weight: 300;
    color: rgba(256, 256, 256, 0.8);
  }
  .fa{
    font-size: 1.4rem;
    color: white;
  }
  .Track-action:hover {
    background-color: rgba(221, 199, 217, 0.7);
  }
   .fa:hover{
     color: rgb(96, 59, 204)
   }
   
   