.Playlists-buttons{

    cursor: pointer;
    /* box-shadow: 0px 5px; */
    margin: 4px 7px;
    padding:20px 10px;
    background-color:rgba(108, 65, 233, .7);
    color: white; 
    border-radius: 5px; 
    max-width: 60px;
    text-transform: uppercase;
    font-size: 0.7rem;
    text-decoration: none;
    
}
 .Playlists-buttons:hover{
     background-color:rgba(148, 137, 182, 0.7); ;
 }
 .Playlists-buttons:active{
     background-color: rgba(202, 197, 218, 0.7);
     
     padding-top: 25px;
 }

 .Buttons {
     text-align: center;
 }
 a.Playlists-buttons{
border-top: 2px solid  grey;
border-left: 2px solid grey;
padding: 19px 3px 35px 3px;

 }