.Playlists {
    display: flex;
    flex-direction: column;
  
    align-items: center;
    overflow-y: scroll;
    width: 37%;
    max-height: 950px;
    padding: 2.27rem 1.16rem;
    background-color: rgba(1, 12, 63, 0.7);
    box-shadow: 0 4px 2px 2px #000000;
  }
  
  .Playlists input {
    right: 20px;
    width: 100%;
    border: 0;
    outline: 0;
    background: transparent;
    border-bottom: 1px solid white;
    font-family: sans-serif;
    font-size: 1.3rem;
    font-weight: 500 ;
    color: #fff;
    text-align: center;
    
  } 

   /* ::placeholder{
    color:white;
  }  */
  
  .Playlists-save {
    
    cursor: pointer;
    width: 100%;
    padding: 1rem .5rem;
    border-radius: 4px;
    border-width: 0px;
    margin: 1.27rem auto;
    background-color: #6c41ec;
    text-align: center;
    font-size: .83rem;
    transition: background-color .25s;
    color: #fff;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 1px;
  }
  
  .Playlists-save:hover {
    background-color: rgba(124, 107, 187, 0.7);
    font-size: 1rem;
    padding: 1.2rem;
  }
  
  .Playlists-save:active {
    background-color: rgba(124, 107, 187, 0.7);
    font-size: 1.1rem;
    padding: 1.3rem;
  }
  ::-webkit-scrollbar {
      width: 0px;
      background: transparent;
  }
   .Playlists-input{
     padding-top: 10%;
   } 

   .AddItems-topbar{
     text-align: center;
     position: sticky;
     top: -30px;
     left: 0px;
     z-index: 13;
     border-radius: 4px;
     background-color: rgba(27, 14, 66, 0.9);
     /* transform: translateZ(0); */
    /* -webkit-transform: translateZ(0); */
    /* overflow:auto;  */
   }
   .AddItems-playlists{
     border-top: 1.5px solid white;
     position: relative;
     top: 40px;
     width: 100%;
     /* text-align: center; */
   }

   h3.Playlists-header{
     margin-top: 2.5rem
   }
  
  @media only screen and (max-width: 700px) {
    .Playlists {
      width: 100%;
      padding: 2.27rem .76rem
    }
  }