
*{
/* margin:0;
padding: 0; */
box-sizing: border-box;
}

body,
html,
#root {
  height: 100%;
}

html {
  font-size: 18px;
}

h1 {
  /* without marrgin set to 0 we somehow get margin on the top/bottom */
  margin: 0;
  padding: 3rem 2px 1rem 2px;
  background-color: #010c3f;
  text-align: center;
  font-family: 'Poppins', sans-serif;
  font-size: 1.88rem;
  color: #fff;
  /* position: relative; */
  
}
 
h1 .highlight {
  color: rgb(108, 65, 236);
  /* for breaking long user name so it dosent go outside the element: */
  
}

h2 {
  font-family: 'Poppins', sans-serif;
  font-size: 1.55rem;
}

h4{
  margin: 0;
  padding: 1rem 1px 1rem 2px;
  background-color: #010c3f;
  text-align: center;
  font-family: 'Poppins', sans-serif;
  
  color: #fff;
}

.section{
  text-align: center;
  height: 100%;
  scroll-snap-type: start;
}
.Main{
  overflow:visible;  }
.App {
 
  overflow: auto;
  height: 100%;
  /* position: relative; */
  /* display:flex; */
  /* flex-direction: column; */

  padding: 0 14% 10% 14%;
  background-image: url('./dj_mouse-wallpaper-1920x1200.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  /* background-position: ; */
  font-family: 'Work Sans', sans-serif;
  font-weight: 500;
  color: white;
}

.No-auth{
   background-color: rgba(118, 101, 170, 0.4);
   /* position: absolute no bueno here */
  position: fixed;
  top: 0;
  left:0;
  height: 100%;
  width: 100%;
  z-index: 15;
 
}
.App-playlist {
  padding-top: 10%;
  display: flex;
  justify-content: space-between;
  width: 100%;
}
#btnScrollToTop{
  
  position: fixed;
  right:10px;
  bottom:10px;
  width: 40px;
  height:40px;
  z-index:170;
  border-radius: 4px;
  background-color: rgb(1, 12, 63, 0.3);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
  color: white;
  outline: none;
  cursor: pointer;
  border: none;
  text-align: center;
  transform: translateZ(0);
  -webkit-transform: translateZ(0);
  

}

#btnScrollToTop:hover{
background-color: rgba(1, 12, 63, 0.7);
}

.material-icons{
  margin-top: 7px;
}
h2.header{
  text-align: center;
  padding-top: 5rem;
  padding-bottom: 1.5rem;
  margin-bottom: 0;
  color: white
}

/* .material-icons:hover{
  color:rgb(104, 82, 170);
} */
.Main{
  overflow:visible;  }

.two{
  padding-top: 150px;
}

@media only screen and (max-width: 700px) {
  .App-playlist {
    align-items: center;
    flex-direction: column;
  }
  .App{
    padding: 0 10% 10% 10%;
    background-image: url('./dj_mouse-wallpaper-mobile.jpg');
    background-size: cover;
    background-attachment: fixed;
    background-repeat: no-repeat
  }
  h2.header{
    color: rgb(108, 65, 236)
  }
}
