.SearchBar {
    display: flex;
    /* flex-direction: row; */
    /* align-items: center; */
    justify-content: center;
    padding-top: .7rem;
    margin-bottom: .33rem;
  }
  
  .SearchBar input {
    width: 287px;
    padding: .88rem 0;
    border: 3px solid  rgb(137, 120, 146);
    border-radius: 3px;
    /* margin-bottom: 2.22rem; */
    /* color: #010c3f; */
    color:  rgba(245, 242, 253);
    text-align: center;
    font-size: 1.3rem;
    background: rgba(91, 68, 112, 0.6);
    font-weight: 500;
  }
  
  .SearchBar input:focus {
    outline: none;
  }
   .SearchBar ::placeholder{
     color: rgb(204, 190, 211)
   }
  
  .SearchButton {
    
    cursor: pointer;
    /* width: ; */
    padding: .77rem 1.8rem;
    /* margin-left: 1px; */
    border-radius: 4px;
    text-align: center;
    font-size: .833rem;
    transition: background-color .25s;
    border: 0px;
    color: #fff;
    font-weight: 500;
    background-color: rgba(108, 65, 233, .7)
    ;
    
  }
  
  .SearchButton:hover {
    background-color: rgba(221, 199, 217, 0.7);
    
  }
   .fas:hover{
     color: blue
   }
  